@use '@miq/styles/src/mixins' as *;

.product-staff-update-view {
  .miq-staff-view-main {
    .p-info {
      .p-name {
        font-size: 16px;
      }

      .p-category {
        font-size: 12px;
        color: var(--gray-700);
      }
    }

    .query-buttons {
      button {
        margin-right: 0.5rem;
      }
    }

    @include md-down {
      .query-buttons {
        margin-top: 0.5rem;
        display: flex;
        justify-content: space-between;
      }

      .d-flex:first-child {
        // flex-direction: column-reverse;
      }
    }
  }
}

.p-card-list-items {
  .p-card-list-item {
    &:hover {
      background-color: var(--blue-50, white);
    }
  }

  .active {
    .p-card-list-item {
      background-color: var(--blue-100, white);
    }
  }
}

.p-card-list-item {
  border-radius: var(--border-radius);

  font-weight: normal;

  .p-info {
    .p-category {
      font-size: 12px;
      color: var(--gray-700);
    }
  }

  img {
    margin-right: 4px;
    max-width: 45px;
    border-radius: var(--border-radius, 4px);
  }
}

.p-image-card {
  .miq-view-section-header {
    margin-bottom: 0 !important;
    border-bottom: 0 !important;
  }
  .miq-view-section-body {
    padding: 0 !important;
  }
}

.p-cover-update-form {
  .btn {
    border: none;
    padding: 0;
    width: 100%;
  }
}
